'use client';
import { useState, useEffect } from 'react';

export default function ApiKeyModal({ apiKey, setApiKey }) {
  const [visible, setVisible] = useState(false);
  const [tempKey, setTempKey] = useState('');

  useEffect(() => {
    if (!apiKey) setVisible(true);
  }, [apiKey]);

  const saveKey = () => {
    if (tempKey.trim()) {
      localStorage.setItem('APIFY_KEY', tempKey.trim());
      setApiKey(tempKey.trim());
      setVisible(false);
    }
  };

  return visible ? (
    <div className="fixed inset-0 bg-black bg-opacity-70 flex items-center justify-center">
      <div className="bg-[#161b22] p-6 rounded-md max-w-sm w-full space-y-4">
        <h2 className="text-xl font-semibold text-[#58a6ff]">Enter Apify API Key</h2>
        <input
          type="text"
          value={tempKey}
          onChange={(e) => setTempKey(e.target.value)}
          placeholder="sk_xxxx"
          className="w-full px-3 py-2 bg-[#0d1117] text-[#c9d1d9] border border-gray-600 rounded focus:ring-2 focus:ring-[#58a6ff]"
        />
        <button
          onClick={saveKey}
          className="w-full px-4 py-2 bg-[#58a6ff] text-black rounded hover:scale-105 transition"
        >
          Save
        </button>
      </div>
    </div>
  ) : null;
}
